<template>
  <!--IP-->
  <div class='ip_query page_main'>
    <p class='ip_title'>IP</p>
    <div class='ip_address_box'>
      <!---->
      <div class="IpAddressDt" id="IpAddressDt"></div>
    </div>
    <p class='ip_title'>IP</p>
    <div class='detailed_information'>
      <ul class='division'>
        <li><span>{{ this.translates.ip }}</span><span>{{ this.data[0].ip }}</span></li>
        <li><span>{{ this.translates.correctness }}</span> <el-rate
          v-model='accuracy'
          disabled
          style='display: inline-block'
          text-color='#ff9900'>
        </el-rate></li>
        <li><span>{{ this.translates.owner }}</span><span>{{ this.data[0].owner }}</span></li>
      </ul>
      <ul class='division'>
        <li><span>{{ this.translates.accuracy }}</span><span>{{ this.data[0].accuracy }}</span></li>
        <li><span>{{ this.translates.consistency }}</span> <el-rate
          v-model='uniformity'
          disabled
          style='display: inline-block'
          text-color='#ff9900'>
        </el-rate></li>
      </ul>
      <p class='subtitle'>{{ this.translates.multiareas }}</p>
      <div class='possible'>
        <div class='region'>
          <p>1</p>
          <ul>
            <li><span>{{ this.translates.radius }}</span><span>{{ this.data[0].multiareas[0].radius }}</span></li>
            <li><span>{{ this.translates.lat }}</span><span>{{ this.data[0].multiareas[0].lat }}</span></li>
            <li><span>{{ this.translates.lng }}</span><span>{{ this.data[0].multiareas[0].lng }}</span></li>
            <li><span>{{ this.translates.detailedaddress }}</span><span>{{ this.data[0].multiareas[0].detailedaddress }}</span></li>
            <li><span>{{ this.translates.address }}</span><span>{{ this.data[0].multiareas[0].address }}</span></li>
          </ul>
        </div>
        <div class='region'>
          <p>2</p>
          <ul>
            <li><span>{{ this.translates.radius }}</span><span>{{ this.data[0].multiareas[1].radius }}</span></li>
            <li><span>{{ this.translates.lat }}</span><span>{{ this.data[0].multiareas[1].lat }}</span></li>
            <li><span>{{ this.translates.lng }}</span><span>{{ this.data[0].multiareas[1].lng }}</span></li>
            <li><span>{{ this.translates.detailedaddress }}</span><span>{{ this.data[0].multiareas[1].detailedaddress }}</span></li>
            <li><span>{{ this.translates.address }}</span><span>{{ this.data[0].multiareas[1].address }}</span></li>
          </ul>
        </div>
      </div>
      <!--<p class='subtitle'></p>-->
      <!--<ul class='explain'>-->
        <!--<li>1.</li>-->
      <!--</ul>-->
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.IP_PLUS
      },
      searchKey: {
        type: String,
        default: '192.168.0.1'
      }
    },
    data () {
      return {
        accuracy: '', // 
        uniformity: '', // 
        data: [],
        translates: {},
        longitude: '',
        latitude: ''
      }
    },
    created () {
      gddmgr.commonSearchApi(this.searchType, { isDemo: true, searchKey: this.searchKey })
        .then(res => {
          // console.log('commonSearchApi NETWORK_TRACES:' + JSON.stringify(res))
          this.resData = res.resData
          this.data = res.resData.rawData
          this.translates = this.resData.translates || {}
          this.accuracy = this.data[0].correctness
          this.uniformity = this.data[0].consistency
          this.longitude = this.data[0].multiareas[1].lng
          this.latitude = this.data[0].multiareas[1].lat
          console.log('IP111111', this.resData)
          console.log('IP222222', this.data)
          console.log('IP333333', this.translates)
          console.log('IP1111111111', this.longitude, this.latitude)

          setTimeout( () => {
            this.loadMap()
          }, 0)
        })
    },
    mounted () {

    },
    methods: {
      loadMap () {
        let map = new BMap.Map('IpAddressDt') //  
        let point = new BMap.Point(this.longitude, this.latitude) // 
        map.centerAndZoom(point, 15) //      
        map.enableScrollWheelZoom(true) //   

        // ,
        function addMarker (point) {
          let marker = new BMap.Marker(point)
          map.addOverlay(marker) //                         
          marker.setAnimation(BMAP_ANIMATION_BOUNCE) //     
        }
        // 2
        let bounds = map.getBounds()
        let sw = bounds.getSouthWest()
        let ne = bounds.getNorthEast()
        let lngSpan = Math.abs(sw.lng - ne.lng)
        let latSpan = Math.abs(ne.lat - sw.lat)
        for (let i = 0; i < 2; i++) {
          let point = new BMap.Point(sw.lng + lngSpan * (Math.random() * 0.7), ne.lat - latSpan * (Math.random() * 0.7))
          addMarker(point)
        }

        /*  */
        map.addControl(new BMap.ScaleControl({anchor:BMAP_ANCHOR_TOP_LEFT}))
        map.addControl(new BMap.NavigationControl())
        map.addControl(new BMap.NavigationControl({anchor: BMAP_ANCHOR_BOTTOM_RIGHT, type:BMAP_NAVIGATION_CONTROL_SMALL}))
      }
    }
  }
</script>

<style scoped>
  @import '../search.css';

  .ip_query {
    font-family:MicrosoftYaHei;
  }
  .ip_query .ip_title {
    font-size:20px;
    color:rgba(46,46,48,1);
    line-height:20px;
    margin-bottom: 25px;
    font-weight: bold;
  }
  .ip_query .ip_address_box {
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(6,0,1,0.1);
    height: 450px;
    margin-bottom: 20px;
    padding: 20px;
   }
  .IpAddressDt {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
  }
  .ip_query .detailed_information {
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(6,0,1,0.1);
    padding: 30px 50px;
    overflow: hidden;
  }
  .ip_query .detailed_information  .division {
    overflow: hidden;
  }
  .ip_query .detailed_information  .division li{
    float: left;
    margin-right: 20px;
    margin-bottom: 20px;
    font-size:16px;
    color:rgba(144,144,144,1);
    line-height: 32px;
    min-width: 300px;
  }
  .ip_query .detailed_information .subtitle {
    font-size: 20px;
    color: rgba(102,102,102,1);
    line-height: 40px;
    margin: 10px 0;
  }
  .ip_query .detailed_information .possible {
    display: flex;
  }
  .ip_query .detailed_information .possible .region {
    flex: 1;
  }
  .ip_query .detailed_information .possible .region p {
    font-size:16px;
    color:rgba(102,102,102,1);
    line-height: 20px;
    margin-bottom: 10px;
  }
  .ip_query .detailed_information .possible .region li {
    font-size:16px;
    color:rgba(144,144,144,1);
    line-height:30px;
    margin-bottom: 10px;
  }
  .ip_query .detailed_information .explain li {
    font-size:16px;
    color:rgba(144,144,144,1);
    line-height:30px;
  }
</style>



