<template>
  <!--  -->
  <div class="weibo_details_main page_main">
    <div class="header_img"><img :src="this.data.datas[0].avatarUrl"></div>
    <div class="data_panel">
      <p class="theme_title">{{ this.data.datas[0].userName }}</p>
      <div class="short_list">
        <ul>
          <li><span>{{ this.data.datas[0].location }}</span></li>
          <li><span>{{ this.data.datas[0].postCount }}</span></li>
          <li><span>{{ this.data.datas[0].followCount }}</span></li>
          <li><span>{{ this.data.datas[0].idType }}</span></li>
        </ul>
      </div>
      <div class="short_list">
        <ul>
          <li><span>{{ this.data.datas[0].gender }}</span></li>
          <li><span>{{ this.data.datas[0].fansCount }}</span></li>
          <li><span>{{ this.data.datas[0].friendCount }}</span></li>
          <li><span v-if="this.data.datas[0].idVerified"></span><span v-else></span></li>
        </ul>
      </div>
      <p><span>{{ this.data.datas[0].registerDate }}</span></p>
      <p><span>{{ this.data.datas[0].updateDate }}</span></p>
      <p><span class="weibo_link"><a target="_blank" :href="this.data.datas[0].url">{{ this.data.datas[0].url }}</a></span></p>
      <p><span>{{ this.data.datas[0].idVerifiedInfo }}</span></p>
      <p><span>{{ this.data.datas[0].biography }}</span></p>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.EX_WEIBO_DETAIL
      },
      searchKey: {
        type: String,
        default: ''
      }
    },
    data () {
      return {
        data: {}
      }
    },
    created () {
      let params = {
        id: this.searchKey
      }
      gapi.weiboDetail(params).then(data => {
        // console.log("---------weibo detail:" + JSON.stringify(data))
        this.data = data
        // console.log('', this.data.datas[0])
      })
    }
  }
</script>

<style scoped>
  @import '../search.css';

  .weibo_details_main {
    margin: 40px 160px 0;
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(7,0,2,0.1);
    padding: 50px 30px;
  }
  .weibo_details_main .header_img {
    display: inline-block;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin-right: 50px;
    vertical-align: top;
    margin-bottom: 30px;
  }
  .weibo_details_main .header_img img {
    display: block;
    width: 100%;
    height: 100%;
  }
  .weibo_details_main  .data_panel {
    display: inline-block;
    vertical-align: top;
    overflow: hidden;
    width: 83%;
  }
  .weibo_details_main .data_panel p {
    margin-bottom: 30px;
    color: #909090;
    font-size: 16px;
    line-height: 32px;
  }
  .weibo_details_main  .data_panel .theme_title {
    color: rgba(102,102,102,1);
    font-size: 20px;
  }
  .weibo_details_main .data_panel span {
    color: rgba(102,102,102,1);
  }
  .weibo_details_main .data_panel .weibo_link {
    color: rgba(59,151,212,1);
  }
  .weibo_details_main .data_panel .short_list {
    overflow: hidden;
    margin-bottom: 30px;
  }
  .weibo_details_main .data_panel .short_list li {
    float: left;
    margin-right: 10px;
    color:rgba(144,144,144,1);
    width: 240px;
    margin-bottom: 20px;
  }


</style>


