  import bus from '../common/bus'

  export default {
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.EX_BUSINESS_DETAIL
      },
      searchKey: {
        type: Number,
        default: ''
      }
    },
    data () {
      return {
        tableData: [
          {
            name: '',
            capital: '',
            time: '2016-05-02',
            state: ''
          }
        ],
        // 1
        // 2
        // 3
        // 4
        changereCord: [
          {
            changeItem: '',
            createTime: '',
            contentBefore: '',
            contentAfter: '',
            changeTime: ''
          }
        ],
        changereCordTotalRowNum: 1,
        changereCordscrollId: 1,
        name: '',
        // 5
        // 6
        ObtainNumber: ''
        // 7
        // 8
        // 9
        // 10
        // 11
        // 12
        // 13
        // 14
      }
    },
    watch: {
      name: function (value, oldValue) {
        // console.log(value, '2222', oldValue)
      }
    },
    created () {
      bus.$on('obtNmae', msg => {
        this.name = msg.name
        this.obtDetailsData()
      })
    },
    methods: {
      obtDetailsData () {
        this.obtAbnormal() // 
        this.obtAdministrat() // 
        this.obtBranchme() // 
        this.obtainChangeRecode() // 
        this.obtDishonestPerson() // 5
        this.obtFullDimension() // 6
        this.obtLegalproceed() // 7
        this.obtOutBound() // 8
        this.obtPersonSubJected() // 9
        this.obtEriousvio() // 10
        this.obtPotcheck() // 11
        this.obtTaxnotice() // 12
        this.obtTaxrating() // 13
        this.obtWebsitefiling() // 14
      },
      // 1  
      obtAbnormal () {
        let params1 = {
          id: this.searchKey
        }
        gapi.getBusinessAbnormal(params1).then(res => {
          console.log('####' + JSON.stringify(res))
          this.resData = res.resData
          this.data = res.resData.rawData
          this.translates = this.resData.translates || {}
          console.log('', this.resData)
          console.log('', this.data)
          console.log('', this.translates)
        })
      },
      // 2  
      obtAdministrat () {
        let params2 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessAdministrat(params2).then(res => {
          console.log('####', res)
        })
      },
      // 3
      obtBranchme () {
        let params3 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessBranchme(params3).then(res => {
          console.log('####', res)
        })
      },
      // 4  
      obtainChangeRecode () {
        let params4 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: this.changereCordscrollId
        }
        gapi.getBusinessChangereCord(params4).then(res => {
          console.log('####', res)
          this.changereCord = res.data.data.resultList
          this.changereCordTotalRowNum = res.data.data.totalRowNum
        })
      },
      // 5
      obtDishonestPerson () {
        let params5 = {
          name: this.name,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessDishonestPerson(params5).then(res => {
          console.log('####', res)
        })
      },
      // 6
      obtFullDimension () {
        let params6 = {
          id: this.searchKey,
          name: this.name,
          pageSize: 5,
          scrollId: 1
        }
        console.log('', params6)
        gapi.getBusinessfulldimension(params6).then(res => {
          console.log('####', res)
          this.fulldimension = res.data.data.result
          this.EssentialInformation = this.fulldimension.EssentialInformation
          this.ObtainNumber = this.fulldimension.ObtainNumber
          this.itemK = this.EssentialInformation.headers
          console.log('11111', this.fulldimension, this.EssentialInformation, this.itemK)
          for (let j in this.itemK) {
            this.FullDimensionAllKData = this.itemK[j].fieldCH
            console.log(this.FullDimensionAllKData)
          }
        })
      },
      // 7
      obtLegalproceed () {
        let params7 = {
          name: this.name,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessLegalproceed(params7).then(res => {
