        let params12 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessTaxnotice(params12).then(res => {
          console.log('####', res)
        })
      },
      // 13
      obtTaxrating () {
        let params13 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessTaxrating(params13).then(res => {
          console.log('####', res)
        })
      },
      // 14
      obtWebsitefiling () {
        let params14 = {
          id: this.searchKey,
          pageSize: 5,
          scrollId: 1
        }
        gapi.getBusinessWebsitefiling(params14).then(res => {
          console.log('####', res)
        })
      },

      handleCurrentChange (val) { // 
        this.changereCordTotalRowNum = val
        this.obtainChangeRecode()
      }
    }
  }
