    background: url("../../assets/images/search/collectioned.png")no-repeat 100%;
    width: 30px;
    height: 35px;
  }
  .local_query ul .large_list ul .small_list {
    min-width: 400px;
    /*width: 400px;*/
    vertical-align: top;
    display: inline-block;
    margin: 10px;
    overflow: hidden;
  }
</style>



